.categorymodal-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  background-color: #282828;
  border-radius: 5px;
  width: 32rem;
  height: 18rem; }
  .categorymodal-container-title {
    text-align: center;
    font-size: 1.4rem;
    color: white; }
  .categorymodal-container-name {
    margin-left: 2rem;
    font-size: 1.2rem;
    height: 0.8rem;
    margin-top: 2.5rem;
    color: white; }
  .categorymodal-container > input {
    margin-left: 2rem;
    width: 80%;
    height: 2rem;
    border: none;
    padding-left: 0.5rem;
    outline: #3939ac;
    font-size: 1rem; }
  .categorymodal-container-cancel {
    cursor: pointer;
    outline: none;
    border: 1px solid #353535;
    background-color: #353535;
    color: white;
    font-size: 1rem;
    box-sizing: border-box;
    padding: 0.5rem 1rem 0.5rem 1rem;
    margin-top: 3rem;
    margin-left: 17rem;
    margin-right: 1rem; }
  .categorymodal-container-create {
    cursor: pointer;
    outline: none;
    border: none;
    background-color: #7272cb;
    color: white;
    font-size: 1rem;
    box-sizing: border-box;
    padding: 0.5rem 1rem 0.5rem 1rem;
    border-radius: 3px; }
