.userbans {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  background-color: #282828;
  border-radius: 5px;
  width: 24rem;
  height: 12rem; }
  .userbans > h1 {
    text-align: center;
    color: white;
    font-size: 1.4rem;
    margin-top: 2rem; }
  .userbans > p {
    margin-left: 3rem;
    color: white;
    margin-top: 2rem;
    font-size: 1.2rem; }
  .userbans-activity {
    display: flex;
    flex-direction: row; }
  .userbans-cancel {
    cursor: pointer;
    outline: none;
    border: none;
    background-color: black;
    color: white;
    font-size: 1rem;
    box-sizing: border-box;
    padding: 0.5rem 1rem 0.5rem 1rem;
    width: 5rem;
    height: 2.1rem;
    margin-top: 2rem;
    margin-left: 6.5rem; }
  .userbans-save {
    cursor: pointer;
    outline: none;
    border: none;
    background-color: #7272cb;
    color: white;
    font-size: 1rem;
    box-sizing: border-box;
    padding: 0.5rem 1rem 0.5rem 1rem;
    width: 5rem;
    height: 2rem;
    margin-top: 2rem;
    margin-left: 1rem; }
